import React, {useEffect, useState} from 'react';
import Text from 'views/Text/Text/v1/Text';
import Box from 'views/Box/v1/Box.js';
import {log} from 'utilities/Measure';

const hrStyles = {backgroundColor: 'white', opacity: "0.5", height: '1px'};
export default (({title, subtitle, mods}) => {
    return <Box maxWidth="mediaL">
        <Text padding="0px 0px 0px 0px" tag="h1" highlight="primary" 
        className="width-full text-center" color="text" fontSize="2.5">{title}</Text>
        {subtitle && <Text tag="h3" className="block text-center"  color="text">{subtitle}</Text>}
        <hr style={hrStyles}/>
    </Box>
})

